@charset "utf-8";

body{
    font-family:"맑은 고딕";font-size:12px;
    color:#333;
}
ol,li,ul{list-style:none;}

article > h3{
    text-align:center;
    font-size:24px;
}
/* product list서식  */
.product_list{
  width:1050px;
  margin:0px auto;
  display:flex;
  padding:0px;
  justify-content: space-evenly;
}
.product_list > li{
  width:249px;height:434px;
  position:relative;
    overflow:hidden;
}
.p_title{
    font-weight:bold;
    font-size:16px;
    position:absolute;bottom:80px;
    padding-top:10px;
    width:100%;height:40px;
    background:#fff;
}
.p_price{
    color:#f00;
    font-weight:bold;
    font-size:18px;
}
.product_list > li img{
    height:320px;
    transition:0.5s;
}
.product_list > li:hover img{
    transform:scale(1.1);
}
.product_list .icon{
  position:absolute;
  color:#fff;
  padding:3px 12px;
  background:rgba(230, 22, 22, 0.7);
  border-radius:50%;
  bottom:10px;right:10px;
  font-size:30px;
}
